<template>
  <el-dialog @close="resetInfo" :title="specsData.id>0?'编辑':'新增'" :visible.sync="info.isShow">
    <el-form ref="specsForm" :model="specsData" :rules="rules" label-width="80px">
      <el-form-item label="属性名" prop="specsname">
        <el-input v-model.trim="specsData.specsname" placeholder="请输入属性名"></el-input>
      </el-form-item>
      <el-form-item label="属性值" prop="attrs">
        <div class="item" v-for="(item,index) in specsArr" :key="index">
          <el-input v-model.trim="specsArr[index]" placeholder="请输入属性值"></el-input>
          <el-button @click="addAttr" type="success" size="mini" v-if="index===0">添加</el-button>
          <el-button type="danger" size="mini" v-else>删除</el-button>
        </div>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-radio-group v-model="specsData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="info.isShow = false">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: ["info"],
  data() {
      // 自定表单校验规则
      const validAttrs=(rule,value,callback)=>{
          // 假设所有的属性输入框都输入了值
          let flag=true
          this.specsArr.forEach(item=>{
              if(item===''){
                  // 推翻假设
                  flag=false
              }
          })
          if(!flag){
              return callback(new Error('请输入规格属性'))
          }
          callback()
      }
    return {
      // 和表单进行绑定的数据
      specsData: {
        id:'', 
        // 规格名称
        specsname: "",
        // 规格属性可选值(a,b,c)
        attrs: "",
        // 状态
        status: ""
      },
      //   表单校验
      rules: {
        specsname:{required:true,message:'请输入属性名',trigger:'blur'},
        status:{required:true,message:'请选择状态',trigger:'blur'},
        attrs:{validator:validAttrs,trigger:'blur'}
      },
      // 规格可选值的数组
      specsArr: [""]
    };
  },
  methods: {
    addAttr() {
      if (this.specsArr.length >= 5) {
        this.$error("最多只能添加5个属性值");
        return false;
      }
      this.specsArr.push("");
    },
    // 提交表单
    submitForm(){
        this.$refs.specsForm.validate(valid=>{
            if(valid){
                // ['a','b','c']=>a,b,c
                this.specsData.attrs=this.specsArr.join(',')
                const url=this.specsData.id>0?'/specsedit':'/specsadd'
                this.$http.post(url,this.specsData).then(res=>{
                    if(res.code===200){
                        this.$success(res.msg)
                        // 1-重置信息
                        this.resetInfo()
                        // 2-刷新数据列表:通过自定义事件通知父组件更新数据列表
                        this.$emit('updateList')
                    }else{
                        this.$error(res.msg)
                    }
                })
            }
        })
    },
    // 设置表单数据
    setInfo(obj){
        this.specsData={...obj}
        this.specsArr=obj.attrs
    },
    // 重置信息
    resetInfo(){
        // 1-重置表单
        this.specsData={specsname:'',status:'',attrs:''}
        this.specsArr=['']
        // 2-关闭模态框
        this.info.isShow=false
    }
  }
};
</script>

<style scoped>
.item {
  display: flex;
  margin: 5px 0;
}
.item button {
  margin-left: 10px;
}
</style>


