<template>
  <el-card>
    <el-button @click="info.isShow=true" type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
    <el-divider />
    <el-table :data="specsList" style="width: 100%">
      <el-table-column prop="id" label="ID" min-width="80"></el-table-column>
      <el-table-column prop="specsname" label="规格名称" min-width="180"></el-table-column>
      <el-table-column prop="attrs" label="规格可选值" min-width="180">
          <template v-slot="$props">
               <el-tag type="success" v-for="(item,index) in $props.row.attrs" :key="index">{{item}}</el-tag>
          </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="180">
        <template v-slot="$props">
          <el-tag type="success" v-if="$props.row.status===1">正常</el-tag>
          <el-tag type="danger" v-else>正常</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
        <template v-slot="$props">
          <el-button @click="edit($props.row)" type="success" circle size="mini" icon="el-icon-edit"></el-button>
          <el-button @click="del($props.row.id)" type="danger" circle size="mini" icon="el-icon-delete"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
    background
    layout="prev, pager, next"
    :total="specsTotal"
    :page-size="size"
    @current-change="pageChange"
    >
    </el-pagination>

    <!-- 添加/编辑 -->
    <Dialog ref="dialog" :info="info" @updateList="updateList"/>
  </el-card>
</template>

<script>
import Dialog from './Dialog'
import { mapState,mapActions } from 'vuex'
export default {
  components:{
    Dialog
  },
  data(){
    return  {
      // 传递给子组件的数据
      info:{
        isShow:false
      },
      // 页码
      page:1,
      // 每页显示数据条数
      size:1
    }
  },  
  computed:{
    ...mapState({specsList:state=>state.specs.specsList,specsTotal:state=>state.specs.specsTotal})
  },
  methods:{
    ...mapActions('specs',['getSpecsListAction','getSpecsTotalAction']),
    // 监听分页按钮的点击
    pageChange(page){
      // 1-更新this.page
      this.page=page
      // 2-更新数据列表
      this.getSpecsListAction({page:this.page,size:this.size})
    },
    // 自定义事件updateList的处理函数
    updateList(){
      this.getSpecsListAction({page:this.page,size:this.size})
      this.getSpecsTotalAction()
    },
    // 编辑
    edit(obj){
      // 设置表单数据
      this.$refs.dialog.setInfo(obj)
      // 显示编辑组件
      this.info.isShow=true
    },
    // 删除
    del(id){
      this.$myConfirm(async ()=>{
        // 1-发请求,删除数据
        const res=await this.$http.post('/specsdelete',{id})
        if(res.code===200){
          this.$success(res.msg)
              // 2-更新视图
              this.getSpecsListAction({page:this.page,size:this.size})
              this.getSpecsTotalAction()
              return 
          }
          this.$error(res.msg)
      })
    }
  },
  created(){
    this.getSpecsListAction({page:this.page,size:this.size})
    this.getSpecsTotalAction()
  }
}
</script>

<style scoped>
.el-tag{
  margin:0 5px;
}
</style>


